import { FormEvent, useState } from 'react'

import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

import { Envelope, Lock } from 'phosphor-react'
import { Button } from './../components/Button'
import { Checkbox } from './../components/Checkbox'
import { Heading } from './../components/Heading'
import { Text } from './../components/Text'
import { TextInput } from './../components/TextInput'
import { Logo } from './../Logo'

export function SignIn() {
  const[isUserSignedIn, setIsUserSignedIn] = useState(false)

  const notify = () => toast.success("Login realizado!")

  function handleSignIn(event: FormEvent) {
    event.preventDefault()

    setIsUserSignedIn(true)
  }

  return(
    <div className='w-screen h-screen bg-gray-900 flex flex-col items-center justify-center text-gray-100'>
      <header className="flex flex-col items-center">
        <Logo />

        <Heading size='lg' className='mt-4'>
          Ignite Lab
        </Heading>

        <Text size='lg' className='text-gray-400 mt-1'>
          Fa√ßa login e comece a usar!
        </Text>
      </header>

      <form onSubmit={handleSignIn} className='flex flex-col gap-4 items-stretch w-full max-w-sm mt-10'>
        {isUserSignedIn && <ToastContainer autoClose={1000} hideProgressBar={true} theme='colored' />}
        
        <label htmlFor="email" className='flex flex-col gap-3'>
          <Text className='font-semibold'>Endere√ßo de e-mail</Text>
          <TextInput.Root>
            <TextInput.Icon>
              <Envelope />
            </TextInput.Icon>

            <TextInput.Input id='email' required type={'email'} placeholder='Digite seu e-mail' />
          </TextInput.Root>
        </label>

        <label htmlFor="password" className='flex flex-col gap-3'>
          <Text className='font-semibold'>Sua senha</Text>
          <TextInput.Root>
            <TextInput.Icon>
              <Lock />
            </TextInput.Icon>

            <TextInput.Input id='password' required type={'password'} placeholder='**********' />
          </TextInput.Root>
        </label>

        <label htmlFor="remember" className='flex items-center gap-2'>
          <Checkbox id='remember' />
          <Text size='sm' className='text-gray-200 cursor-pointer'>Lembrar de mim por 30 dias.</Text>
        </label>

        <Button type='submit' className='mt-4' onClick={notify}>Entrar na plataforma</Button>
      </form>

      <footer className='flex flex-col items-center gap-4 mt-8'>
        <Text asChild size='sm'>
          <a href='' className='text-gray-400 underline transition-colors hover:text-gray-200'>Esqueceu sua senha?</a>
        </Text>
        <Text asChild size='sm'>
          <a href='' className='text-gray-400 underline transition-colors hover:text-gray-200'>N√£o possui conta? Crie uma agora!</a>
        </Text>
      </footer>
    </div>
  )
}